@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;1,300;1,400&display=swap');

.typography-container {
  font-family: 'Poppins', sans-serif !important;
}

.typography-span {
  color: #777;
}

.Button {
  color: aquamarine;
  background-color: aquamarine;
}

.typography-container {
  text-align: center;
}

.typography_link {
  font-size: 13px;
  color: #385898 !important;
  text-decoration: none !important;
}

@media (max-width: 440px) {
  .general {
    height: auto;
  }
  .typography-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    text-align: center;
  }
  .footer-container {
    margin-top: 24px;
  }
}
