@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;1,300;1,400&display=swap);
body {
  height: 100vh;
  margin: 0;
}

.grid_container_link {
  color: #b1b0b0;
  text-decoration: none !important;
  opacity: 0.5;
}

.footer-typography {
  opacity: 0.5;
}

@media only screen and (min-width:992px) {
    .header-container-full-width {
        width: 100% !important;
        height: 80px;
        background-color: #3b5998;
        background-image: linear-gradient(#4e69a2, #3b5998 60%);
        border-bottom: 1px solid #133783;
    }
}

@media only screen and (max-width:479px) {
    .header_main_logo {
        margin-top: 10px;
        margin-bottom: 10px;
        margin-left: 20px;
    }
}

.header_main_logo {
    margin-top: 20px;
}

.header-container-full-width {
    width: 100% !important;
    background-color: #3b5998;
    background-image: linear-gradient(#4e69a2, #3b5998 60%);
    border-bottom: 1px solid #133783;
}
.typography-container {
  font-family: 'Poppins', sans-serif !important;
}

.typography-span {
  color: #777;
}

.Button {
  color: aquamarine;
  background-color: aquamarine;
}

.typography-container {
  text-align: center;
}

.typography_link {
  font-size: 13px;
  color: #385898 !important;
  text-decoration: none !important;
}

@media (max-width: 440px) {
  .general {
    height: auto;
  }
  .typography-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    text-align: center;
  }
  .footer-container {
    margin-top: 24px;
  }
}

